import api from '@/common/index.js';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
var eindex = {
	content: '',
	navTitle: '',

}
export default {
	components: {
		uniNavBar
	},
	data() {
		return {
			navHeight: 0,
			list: {
				content: '',
				navTitle: '',
				
			}
		}
	},
	onLoad(option) {
		let that=this;
		api.load(this.$t('index').loadmsg)
		eindex=this.list;
		this.getTitleHeight();
		setTimeout(function(){
			that.loadData(option.id);
		},300)

	},

	methods: {
		loadData(id) {
			let that = this;
			that.$ajax.artData(id).then(res => {
				let str = res.info.contents;
				str.replace(/\<img/g, '<img style="max-width:100%;"')
				eindex.content = str.replace(/\<img/g, '<img style="max-width:100%;"');
				eindex.navTitle = res.info.title;
				that.list=eindex;
				uni.hideLoading();
			})
		},
		back() {
			uni.navigateBack({
				delta: 1
			});
		},
		getTitleHeight() {
			let that = this;
			uni.getSystemInfo({
				success: res => {
					console.log(res.screenHeight)
					if (res.screenHeight < 750) {
						that.navHeight = "67px"
						return false;
					}
					if (res.screenHeight < 950) {
						that.navHeight = "77px"
						return false;
					}
					if (res.screenHeight > 1000) {
						that.navHeight = "100px";
						return false;
					}
				}
			})
		}
	}
}
